.team {
    &__container {
        // border: solid 1px $primeBlue;
        padding: 20px 0;
        border-radius: 20px;
        gap: 20px;
        // background-color: lighten($primeBlue, 46%);

        @media screen and (min-width: 900px) {
            padding: 20px 0;
        }
    }

    &__row {
        @media screen and (min-width: 900px) {
            display: flex;
            gap: 20px;
        }
    }

    &__card {
        // text-align: center;
        // height: 100%;
        max-width: 100%;
        padding: 30px;
        border-radius: 20px;
        margin-bottom: 20px;
        border: solid 1px #e2e2e2;
        background-color: #fff;

        @media screen and (min-width: 900px) {
            padding: 50px;
            width: 100%;
            text-align: left;
            display: flex;
            justify-content: space-between;
        }
        p {
            line-height: 28px;
            font-size: 1.2rem;
        }

        &--vertical {
            display: flex;
            flex-direction: column;

            .team__content {
                width: 100%;
            }

            .team__img {
                width: 60%;
                margin: 0 auto 50px;
            }
        }
    }

    &__img {
        background-color: #eee;

        border-radius: 100%;
        margin: 0 auto 50px;
        overflow: hidden;
        height: 100%;
        width: 60%;

        img {
            object-fit: cover;
            height: 100%;
            width: 100%;
            max-width: 100%;
        }

        @media screen and (min-width: 900px) {
            margin: 0 50px 20px auto;
            width: 30%;
        }
    }

    &__content {
        width: 100%;
        margin: 0 auto;

        @media screen and (min-width: 900px) {
            width: 70%;
        }
    }

    &__title {
        font-size: 1.5rem;
        color: #222222;
        margin-bottom: 10px;
        @media screen and (min-width: 900px) {
            font-size: 1.8rem;
        }
    }

    &__roles {
        display: block;
        margin-bottom: 20px;
        color: $primeOrange;
        font-size: 1.2rem;
    }

    &__also {
        color: #222222;
        line-height: 25px;
        width: 800px;
        max-width: 100%;
        margin: 0 auto;
        font-size: 1rem;

        @media screen and (min-width: 900px) {
            font-size: 1.1rem;
        }
    }
}
