.hero {
    // border-radius: 20px;
    // background-color: $primeOrange;
    // box-shadow: 0px 0px 62px rgba(0,0,0,0.1);
    // background: url('/dist/img/Caden.jpg') no-repeat ;
    background-size: cover;
    // margin-top: 20px;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: auto;
    // display: flex;
    align-items: center;
    // padding-top: 150px;
    padding: 0 0 100px 0;

    @media screen and (min-width: 900px) {
        height: 75vh;
        min-height: 700px;
        padding: 0;
    }

    @media screen and (min-height: 900px) {
    }

    &__inner {
        height: 100%;
        align-items: center;
        justify-content: center !important;
    }

    &__overlay {
        background: #37190e;
        // background: linear-gradient(153deg, rgba(231,97,0,0.6082808123249299) 0%, rgba(182,65,29,1) 100%);
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 2;
        opacity: 0.4;
        // display: none;
    }
    &__opener {
        // width: 20%
        text-align: center;
        position: relative;
        z-index: 10;
        // height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        opacity: 0;
        animation: 1s ease 0s normal forwards 1 fadein;
        margin: 150px 0 100px 0;

        @media screen and (min-width: 900px) {
            margin: 50px 0;
            transform: translateY(50px);
        }

        @keyframes fadein {
            0% {
                opacity: 0;
                // transform: translateY(150px);
            }

            16% {
                opacity: 0;

                // transform: translateY(150px);
            }

            100% {
                opacity: 1;
                // transform: translateY(50px);
            }
        }

        @media screen and (min-width: 900px) {
            justify-content: end;
            // padding-right: 50px;
            // text-align: right;
            height: auto;
            display: block;
        }
    }
    &__title {
        color: #fbf3eb;
        text-transform: uppercase;
        font-weight: 700;
        font-family: "Finger Paint", sans-serif;
        font-size: 3.5rem;
        // text-stroke: 1px $primeOrange;
        // -webkit-text-stroke: 1px $primeOrange;
        margin-bottom: 30px;
        // text-shadow: 2px 2px 7px rgba(0, 0, 0, 0.5);
        transition-duration: 0.3s;

        span {
            text-transform: none;
            font-weight: 100;
            font-family: $bodyFont;
            font-size: 2.5rem;
            margin-bottom: 10px;
            display: block;
            letter-spacing: -1px;
            // -webkit-text-stroke-width: 1px;
            // -webkit-text-stroke-color: transparent;
            // color: transparent;
        }

        @media screen and (min-width: 1200px) {
            font-size: 5rem;

            span {
                font-size: 3rem;
            }
        }
    }
    &__cta {
        font-size: 1.5rem;
        text-decoration: none;
        background-color: darken($primeOrange, 10%);
        color: #fff;
        padding: 15px 20px;
        border-radius: 100px;
        display: inline-block;
        transition: 0.3s;
        // border: 1px solid lighten($primeGreen, 20%);
        // box-shadow: 0px 0px 15px lighten($primeGreen, 10%);

        &-icon {
            width: 30px;
        }

        i {
            font-size: 1.5rem;
            margin-right: 10px;
        }

        &:hover {
            background-color: darken($primeOrange, 20%);
            color: $white;
        }

        @media screen and (min-width: 1200px) {
            font-size: 1.8rem;
            i {
                font-size: 1.7rem;
            }
        }
    }

    &__footer {
        position: relative;
        bottom: 0;
        left: 0;
        width: 100%;
        z-index: 1999;
        width: 100%;
        text-align: center;
        animation: 1s ease 0s normal forwards 1 fadein;

        @media screen and (min-width: 900px) {
            position: absolute;
        }

        &-title {
            color: #fff;
            font-size: 1.2rem;
            padding: 10px;
            // text-shadow: 0px 0px 2px #000
            text-transform: uppercase;
            width: 300px;
            display: block;
            margin: 0 auto;
            // background: #d57418;
            border-top-right-radius: 8px;
            border-top-left-radius: 8px;
            // display: none;
        }
        &-cards {
            justify-content: center;
            padding: 0px;
            // background: #9f6a37;
            // background: $primeOrange;
            // mask-image: linear-gradient(to right, transparent, black 20%, black 80%, transparent);
            // -webkit-mask-image: linear-gradient(
            //     to right,
            //     transparent,
            //     black 20%,
            //     black 80%,
            //     transparent
            // ); /* For WebKit browsers */
            @media screen and (min-width: 900px) {
                display: flex;
            }
        }
        &-card {
            color: #fff;
            font-size: 1.3rem;
            padding: 10px;
            // text-shadow: 1px 1px 1px #000;
            // background: #c86609;
            margin: 0 1px;
            position: relative;
            transition-duration: 0.3s;
            // &:hover {
            //     transform: translateY(-8px);
            // }

            @media screen and (min-width: 900px) {
                font-size: 1rem;
            }

            @media screen and (min-width: 1200px) {
                font-size: 1.2rem;
            }

            &::after {
                content: "•";
                right: -4px;
                position: absolute;
                display: none;
            }

            @media screen and (min-width: 900px) {
                padding: 0 20px 20px;

                &::after {
                    content: "•";
                    right: -4px;
                    position: absolute;
                    display: inline-block;
                }
            }

            &:last-child {
                &::after {
                    display: none;
                }
            }
        }
    }
}

#hero-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; /*  Ensure the video covers the entire area */
    z-index: 1; /*  Place the video behind the content */
}

.hero-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    text-align: center;
    z-index: 1; /*  Ensure content is above the video */
    /* Add styling for your content here */
}
