

.accordion__qa--active .accordion__q::after {
    opacity: 1;
    width: 5px
}

.accordion__qa--active .accordion__q i {
    transform: rotate(90deg)
}

.accordion__qa--active .accordion__a {
    max-height: none;
    padding: 20px
}

.accordion__qa {
    margin-bottom: 20px;
}

.accordion__q {
    border: 1px solid #e4e4e4;
    border-radius: 8px;
    background-color: #fff;
    padding: 20px;
    position: relative;
    font-size: 1.3rem;
    display: flex;
    justify-content: space-between;
    font-weight: 600;
    cursor: pointer;
}

.accordion__q i {
    transition-duration: .3s;
    font-size: 1.5rem
}

.accordion__q:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: $primeOrange;
    z-index: 20;
    height: 100%;
    width: 0;
    opacity: 0;
    transition: width .3s,opacity .1s
}

.accordion__a {
    max-height: 0;
    overflow: hidden;
    padding: 0 20px;
    transition: all .35s
}