.section {
    // height: 100vh;
    // display: flex;
    align-items: center;
    padding: 100px 30px;

    @media screen and (min-width: 900px) {
        padding: 100px 30px;
    }
    &__title {
        font-size: 2.2rem;
        // border-bottom: 2px solid;
        margin-bottom: 20px;
        font-family: $sectionTitleFont;
        font-weight: 900;
        color: $primeOrange;
        text-align: center;

        &--blue {
            color: $primeBlue;
        }
        // &--orange {
        //     color: $primeOrange;
        // }
        // &--purple {
        //     color: $primePurple;
        // }
        // &--green {
        //     color: $primeGreen;
        // }

        @media screen and (min-width: 900px) {
            font-size: 2.2rem;
            text-align: left;
        }

        &--center {
            text-align: center;
        }

        &--small {
            font-size: 1.5rem;
        }
    }

    &__subtitle {
        &--blue {
            color: $primeBlue;
        }
    }

    &--interior {
        padding-top: 100px;

        @media screen and (min-width: 900px) {
            padding-top: 200px;
        }
    }

    &--contact {
        @media screen and (min-width: 900px) {
            display: flex;
            justify-content: space-between;
            gap: 20px;
            align-items: flex-start;
            &__col1 {
                width: 50%;
                padding-right: 50px;
            }
            &__col2 {
                width: 50%;
            }
        }
    }

    p {
        font-size: 1.3rem;
        line-height: 30px;
        margin-bottom: 20px;
        color: #1c1c1c;
    }

    &__link {
        margin-bottom: 20px;
        font-size: 1.3rem;
        line-height: 30px;
        display: block;
    }
}
