.header {
    position: absolute;
    z-index: 100;
    background-color: #fff;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
    width: 1350px;
    max-width: 95%;
    border: solid 1px #e4e4e4;
    border-radius: 7px;
    margin: 10px auto 0;
    padding: 10px 10px;

    @media screen and (min-width: 900px) {
        border-radius: 100px;
        margin: 20px auto 0;
    }

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0;
        height: 50px;

        @media screen and (min-width: 900px) {
            padding: 0;
            border-radius: 100%;
            height: unset;
        }
    }

    &__logo {
        width: 250px;
        max-width: 100%;
        &-img {
            // min-width: 200px;
            transition: 0.3s ease;
            filter: hue-rotate(-20deg) saturate(0.8);

            &:hover {
                transform: scale(1.05);
            }
        }

        @media screen and (min-width: 900px) {
            width: 250px;
        }
    }
}
