.mobile-nav {
    position: fixed;
    width: 100%;
    height: 100vh;
    left: 0;
    top: 0;
    background-color: $primeOrange;
    z-index: 100;
    color: #fff;
    padding-top: 100px;
    // display: none;

    &--dark {
        background-color: $bodydark;
        .main-nav__link--mode__icon {
            background-color: $primePurple;
            &:hover {
                background-color: darken($primePurple, 20%);
            }
        }
    }

    &__inner {
        // display: flex;
        justify-content: center;
        align-items: left;
        flex-direction: column;
        padding: 20px;
        text-align: right;
    }

    &__link {
        text-decoration: none;
        margin-bottom: 20px;
        font-size: 1.5rem;
        color: #fff;
        text-align: right;
        position: relative;
        display: inline-block;

        &--active {
            &:after {
                content: "";
                width: 100%;
                height: 4px;
                position: absolute;
                left: 0;
                bottom: -9px;
                background-color: #eee;
                transition: 0.3s;
            }
        }
    }

    &__close {
        position: absolute;
        top: 20px;
        right: 20px;
        font-size: 3rem;
    }
}

.main-nav {
    display: none;
    min-width: 70%;

    @media screen and (min-width: 992px) {
        min-width: unset;
    }
    @media screen and (min-width: 900px) {
        display: flex;
        justify-content: right;
        align-items: center;
        gap: 20px;
        margin-left: 50px;
    }
    &__link {
        text-decoration: none;
        font-size: 1.2rem;
        color: #1c1c1c;
        position: relative;

        &:after {
            content: "";
            width: 0;
            height: 4px;
            position: absolute;
            left: 0;
            bottom: -9px;
            background-color: $primeOrange;
            transition: 0.3s;
        }

        &:hover {
            color: $primeOrange;

            &::after {
                width: 100%;
                background-color: $primeOrange;
            }
        }

        &--active {
            color: $primeOrange;

            &::after {
                width: 100%;
                background-color: $primeOrange;
            }
        }

        &--contact {
            // background-color: #37BB83;
            // color: #fff;
            // padding: 15px 20px;
            // border-radius: 50px;
        }

        &--search {
            display: flex;
            align-items: center;
            width: 60px;
            transition: 0.3s ease;

            &__icon {
                background-color: $primeOrange;
                color: #fff;
                padding: 20px;
                border-radius: 100%;
                transition: 0.3s;
                position: absolute;
                right: 0;
                cursor: pointer;
                &:hover {
                    background-color: darken($primeOrange, 10%);
                    color: #fff;
                }
            }

            &:after {
                display: none;
            }

            input {
                border-radius: 30px;
                border: none;
                height: 60px;
                width: 100%;
                background-color: #d57418;
                color: #fff;
                font-size: 1.2rem;
                // width: 0;
                outline: none;
                padding: 0 20px;
            }

            &--active {
                width: 250px;

                .main-nav__link--search__icon {
                    background-color: darken($primeOrange, 10%);
                }
            }
        }
        &--mobile {
            position: absolute;
            top: 10px;
            right: 80px;
            .main-nav__link--mode__icon {
                padding: 10px 13px;
                font-size: 1.5rem;

                @media screen and (min-width: 900px) {
                    display: none;
                }
            }

            &:after {
                display: none;
            }
        }

        &--mode {
            display: flex;
            align-items: center;
            &__icon {
                background-color: darken($primeOrange, 10%);
                color: #fff;
                /* padding: 20px 25px; */
                border-radius: 100%;
                transition: 0.3s;
                right: 0;
                cursor: pointer;
                width: 65px;
                height: 60px;
                display: flex;
                justify-content: center;
                align-items: center;
                &:hover {
                    background-color: darken($primeOrange, 20%);
                    color: #fff;
                }
            }

            &:after {
                display: none;
            }
        }

        @media screen and (max-width: 1154px) {
            font-size: 1rem;
        }

        @media screen and (max-width: 1044px) {
            font-size: 0.8rem;
        }
    }

    &__hamburger {
        font-size: 3rem;
        display: block;
        padding-left: 20px;

        @media screen and (min-width: 900px) {
            display: none;
        }
    }

    &__mobile-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 10px;
        @media screen and (min-width: 900px) {
            display: none;
        }
    }
}
