.footer {
    width: 100%;
    position: relative;
    background-color: $primePurple;

    iframe {
        border-bottom-left-radius: 20px;
        border-bottom-right-radius: 20px;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        max-width: 100%;
        // height: 100%;
        margin-bottom: 50px;
        margin-top: 50px;
        // display: none;
        @media screen and (min-width: 900px) {
            display: block;
            border-top-left-radius: 0;
            border-top-right-radius: 0;
            margin-top: 0;
        }
    }

    &__inner {
        padding: 20px;
        position: relative;
        z-index: 100;

        @media screen and (min-width: 900px) {
            padding-top: 0;
        }
    }

    &-nav {
        // display: flex;
        flex-direction: column;
        gap: 10px;
        padding-top: 50px;
        li {
            list-style: none;
            margin-bottom: 20px;
        }
        &__link {
            color: #eee;
            font-size: 1.5rem;
            font-weight: 500;
            transition: 0.3s;
            position: relative;
            display: inline-block;
            &::after {
                content: "";
                width: 0;
                height: 4px;
                position: absolute;
                left: 0;
                bottom: -9px;
                background-color: #eee;
                transition: 0.3s;
            }

            &:hover {
                color: #eee;
                &::after {
                    width: 100%;
                }
            }

            &--active {
                color: #eee;

                &::after {
                    width: 100%;
                    background-color: #eee;
                }
            }
        }
    }

    &__content {
        color: #eee;
        padding-top: 50px;

        p {
            margin-bottom: 10px;
            font-size: 1.3rem;
        }
    }

    &__tagline {
        color: #eee;
        padding: 20px 0;
    }

    &__fb-icon {
        font-size: 2rem;
        cursor: pointer;
        transition: 0.3s ease;
        &:hover {
            color: #1778f2;
        }
    }

    &__overlay {
        // background-image: url("/dist/img/Beyond-speech-icon.png");
        // background-repeat: repeat;
        // background-size: 50px;
        background-color: darken($primeGreen, 15%);
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0.9;
        z-index: 10;
    }

    &__overlayBG {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover; /*  Ensure the video covers the entire area */
        z-index: 1; /*  Place the video behind the content */
    }
}
