@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&family=Finger+Paint&family=Roboto+Serif:wght@600;700;800&family=Source+Sans+Pro:wght@300;400;600;700;800&display=swap");

html {
    font-size: 14px;
    @media screen and (min-width: 900px) {
        font-size: 16px;
    }
}

body {
    font-family: $bodyFont;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: $titleFont;
}

strong {
    font-weight: bold;
}

i {
    font-style: italic;
}

h3 {
    font-size: 1.7rem;
    margin-bottom: 10px;
    color: #1c1c1c;
}

a {
    color: $primeBlue;
}

.bsi__ul {
    padding-left: 30px;
    li {
        font-size: 1.3rem;
        margin-bottom: 10px;
        line-height: 30px;
        color: #222222;
        list-style: none;

        &::before {
            content: "\2022"; /* Add content: \2022 is the CSS Code/unicode for a bullet */
            color: $primeBlue; /* Change the color */
            font-weight: bold; /* If you want it to be bold */
            display: inline-block; /* Needed to add space between the bullet and the text */
            width: 1.2rem;
            margin-left: 0em;
            font-size: 2rem;

            @media screen and (min-width: 900px) {
                width: 2rem;
                margin-left: -1em; /* Also needed for space (tweak if needed) */
            }
        }
    }
}
