.dark {
    background-color: $bodydark;
    // $primeOrange: #584da1;
    .header {
        @media screen and (max-width: 900px) {
            // background-color: darken($bodydark, 5%);
        }
        background-color: darken($bodydark, 2%);
        border: solid 1px #12141a;
    }

    .header__logo-img {
        filter: invert(1) hue-rotate(160deg) saturate(0.8);
    }

    .main-nav__link {
        color: #eee;
        &--active {
            color: $primeOrange;
        }
    }

    .hero {
        &__overlay {
            background: darken($bodydark, 2%);
        }
    }

    .hero__cta {
        color: $white;
        &:hover {
            color: $white;
        }
    }

    p {
        color: #eee;
    }

    .aoe__also,
    .aoe__title,
    .pricing__ul li,
    .team__title,
    .form__req-instruction,
    .form__field {
        color: #eee;
    }

    .aoe__card,
    .testimonial__card,
    .testimonial__author,
    .team__card,
    .accordion__q,
    .form__input {
        background-color: #1f212b;
        border-color: #12141a;
        color: #eee;
    }

    .form__input--submit {
        background-color: darken($primeGreen, 10%);
        &:hover {
            background-color: darken($primeGreen, 20%);
        }
    }

    .form__label {
        background-color: #232631;
        color: #eee;
        box-shadow: 0 0 2px 4px #232631;
    }

    .main-nav__hamburger i {
        color: #eee;
    }

    .footer-nav__link {
        color: $white;

        &:hover {
            color: $white;
        }
    }

    a {
        color: lighten($primeBlue, 20%);

        &:hover {
            color: $primeOrange;
        }
    }

    .bsi__ul li {
        color: #eee;
    }
}
.main-body.dark::before {
    content: "";
    position: absolute; /* Positions the pseudo-element relative to the container */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("../../dist/img/Beyond-speech-icon-darker.png");
    background-repeat: repeat;
    opacity: 0.01; /* **This sets the opacity of only the background image layer** */
    z-index: -1; /* Pushes the pseudo-element behind the main content */
}

body.darkMode {
    &::-webkit-scrollbar {
        width: 8px;
        background: $bodydark;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $primePurple;
        border-radius: 20px;
    }

    &::-moz-scrollbar {
        width: 8px;
        background: $bodydark;
    }

    &::-moz-scrollbar-thumb {
        background-color: $primePurple;
        border-radius: 20px;
    }
}
