html {
    scroll-behavior: smooth;
}

.html {
    &--active {
        margin: 0;
        height: 100%;
        overflow: hidden;
    }
}

a {
    text-decoration: none;
    &:hover {
        color: $primeOrange;
    }
}

p {
    a {
        position: relative;
        &::after {
            content: "";
            width: 0;
            height: 4px;
            position: absolute;
            left: 0;
            bottom: -9px;
            background-color: #d26337;
            transition: 0.3s;
        }

        &:hover {
            &::after {
                width: 100%;
            }
        }
    }
}

ul {
    a {
        position: relative;
        &::after {
            content: "";
            width: 0;
            height: 4px;
            position: absolute;
            left: 0;
            bottom: -9px;
            background-color: #d26337;
            transition: 0.3s;
        }

        &:hover {
            &::after {
                width: 100%;
            }
        }
    }
}

body {
    &::-webkit-scrollbar {
        width: 8px;
        background: #fff;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $primeOrange;
        border-radius: 20px;
    }

    &::-moz-scrollbar {
        width: 8px;
        background: #fff;
    }

    &::-moz-scrollbar-thumb {
        background-color: $primeOrange;
        border-radius: 20px;
    }
}

.main-body {
    &::before {
        content: "";
        position: absolute; /* Positions the pseudo-element relative to the container */
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url("../../dist/img/Beyond-speech-icon-darker.png");
        background-repeat: repeat;
        opacity: 1; /* **This sets the opacity of only the background image layer** */
        z-index: -1; /* Pushes the pseudo-element behind the main content */
    }
}

// Text Color Classes for Section Headers

.blue {
    color: $primeBlue;
}

.green {
    color: $primeGreen;
}

.purple {
    color: $primePurple;
}

.orange {
    color: $primeOrange;
}

.main-body {
    transition: 0.3s ease;
    z-index: 1000;
    position: relative;
    background-color: $bodylight;

    &--active {
        position: relative;
        transform: translateY(100px) translateX(-80%);
        z-index: 10000000;
        background: #fff;
        border-radius: 10px;
        overflow: hidden;
        box-shadow: 12px -8px 66px rgba(0, 0, 0, 0.1);
    }
}

img {
    max-width: 100%;
}

::selection {
    background-color: $primeOrange;
    color: #fff;
}

.btt {
    position: absolute;
    bottom: 20px;
    right: 20px;
    width: 60px;
    height: 60px;
    border-radius: 100px;
    background-color: darken($primeGreen, 5%);
    z-index: 100;
    color: lighten($primeGreen, 15%);
    font-size: 2rem;
    cursor: pointer;
    transition: 0.3s ease;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.2);

    &__inner {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
    }

    &:hover {
        background-color: darken($primeGreen, 10%);
        // color: #2b2b2b;
        transform: translateY(-10px);
        box-shadow: 6px 10px 10px rgba(0, 0, 0, 0.1);
    }
}

main {
    animation: 1s ease 0s normal forwards 1 fadein;

    @keyframes fadein {
        0% {
            opacity: 0;
            transform: translateY(50px);
        }

        66% {
            opacity: 0;

            transform: translateY(50px);
        }

        100% {
            opacity: 1;
            transform: translateY(0px);
        }
    }
}

iframe {
    border-radius: 8px;
}

