.preloader {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: #fff;
    z-index: 1000000;
    display: flex;
    justify-content: center;
    align-items: center;
    &::before {
        content: "";
        position: absolute; /* Positions the pseudo-element relative to the container */
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url("/dist/img/Beyond-speech-icon-darker.png");
        background-repeat: repeat;
        opacity: 1; /* **This sets the opacity of only the background image layer** */
        z-index: -1; /* Pushes the pseudo-element behind the main content */
    }
    &__icon-wrapper {
        position: relative;
        width: 80px;
        height: 85px;
        background-color: #eee;
    }
    &__icon-img {
        z-index: 1000;
        position: absolute;
        left: 0;
        top: 0;
    }
    &__icon-bg {
        width: 0;
        height: 100%;
        left: 0;
        top: 0;
        position: absolute;
        background-color: $primeOrange;
        animation: 2s ease 1s normal forwards 1 growin;

        @keyframes growin {
            0% {
                width: 0;
            }

            66% {
                width: 30%;
            }

            85% {
                width: 60%;
            }

            100% {
                width: 100%;
            }
        }
    }

    &--fadeOut {
        animation: 0.5s ease 1s normal forwards 1 fadeout;

        @keyframes fadeout {
            0% {
                opacity: 1;
            }

            100% {
                opacity: 0;
            }
        }
    }
}
