.aoe {
    &__container {
        // border: solid 1px $primeBlue;
        padding: 20px 0;
        border-radius: 20px;
        gap: 20px;
        // background-color: lighten($primeBlue, 46%);

        @media screen and (min-width: 900px) {
            // padding: 50px 0;
            margin-bottom: 20px;
        }
    }

    &__card {
        text-align: center;
        // box-shadow: 0px 0px 32px rgba(0, 0, 0, 0.05);
        background-color: #fff;
        max-width: 100%;
        padding: 50px;
        border-radius: 20px;
        border: solid 1px #dbdbdb;
        margin-bottom: 20px;
        transition-duration: 0.3s;

        @media screen and (min-width: 900px) {
            width: 100%;
            margin-bottom: 0px;

            &:hover {
                // transform: translateY(-20px);
                .aoe__img {
                    transform: translateY(-20px);
                }
            }
        }
    }
    &__img {
        background-color: #e6e8f6;
        width: 150px;
        height: 150px;
        border-radius: 100%;
        margin-bottom: 20px;
        margin-left: auto;
        margin-right: auto;
        transition-duration: 0.3s;
        overflow: hidden;
        @media screen and (min-width: 1300px) {
            width: 200px;
            height: 200px;
        }
    }
    &__title {
        font-size: 1.5rem;
        color: #222222;
        @media screen and (min-width: 900px) {
            font-size: 1.8rem;
        }

        span {
            color: $primeBlue;
        }
    }

    &__also {
        color: #222222;
        line-height: 25px;
        width: 800px;
        max-width: 100%;
        margin: 0 auto;
        font-size: 1rem;

        @media screen and (min-width: 900px) {
            font-size: 1.1rem;
        }
    }
}

.wmud {
    &__container {
        // border: solid 1px $primeOrange;
        border-radius: 20px;
        // background-color: lighten($primeOrange, 52%);

        @media screen and (min-width: 900px) {
            padding: 50px 0;
        }
    }
    &__card {
        @media screen and (min-width: 900px) {
            width: 50%;

            &:first-child {
                padding-right: 50px;
            }
        }
    }

    &__ul {
        padding-left: 20px;
    }
    &__li {
        font-size: 1.3rem;
        margin-bottom: 20px;
        line-height: 30px;
        color: #222222;
        list-style: none;
        &::before {
            content: "\2022"; /* Add content: \2022 is the CSS Code/unicode for a bullet */
            color: $primeOrange; /* Change the color */
            font-weight: bold; /* If you want it to be bold */
            display: inline-block; /* Needed to add space between the bullet and the text */
            width: 1.2rem;
            margin-left: 0em;
            font-size: 2rem;

            @media screen and (min-width: 900px) {
                width: 2rem;
                margin-left: -1em; /* Also needed for space (tweak if needed) */
            }
        }
    }
}

.testimonial {
    &__container {
        // background: #f8f5ff;
        // border: 1px solid #a383e9;
        border-radius: 20px;
    }
    &__card {
        text-align: left;
        font-size: 1rem;
        line-height: 22px;
        background-color: #fff;
        padding: 40px;
        border-radius: 20px;
        // box-shadow: 0 0 50px rgba(0,0,0,.05);
        color: #222222;
        border: solid 1px #dbdbdb;
        transition: 0.3s ease;
        min-height: 200px;
        &:hover {
            transform: scale(1.1);
            z-index: 100;

            // border-color: $primeOrange;
            // transform: translateY(-20px);

            + .testimonial__author {
                transform: scale(1.1) translateY(20px) translateX(10px);
                z-index: 100;
            }

            + .testimonial__rating {
                transform: scale(1.1) translateY(20px) translateX(10px);
                z-index: 100;
            }
        }

        blockquote {
            &::before {
                font-family: "Font Awesome 6 Free";
                content: "\f10d";
                font-weight: 900;
                opacity: 0.2;
            }
            &::after {
                font-family: "Font Awesome 6 Free";
                content: "\f10e";
                font-weight: 900;
                opacity: 0.2;
            }
        }
    }

    &__readmore {
        cursor: pointer;
        color: $primeOrange;
    }

    &__readMoreLink {
        color: $primeOrange;
        &::after {
            display: none;
        }
        &:hover {
            color: darken($primeOrange, 20%);
        }
    }

    &__rating {
        color: $primeOrange;
        font-size: 1.5rem;
    }

    &__author {
        font-weight: 700;
        text-align: right;
        display: inline-block;
        width: auto;
        background-color: #fff;
        padding: 10px 20px 10px 50px;
        border-radius: 50px;
        color: #222;
        border: solid 1px #dbdbdb;
        transition: 0.3s ease;
        margin-top: 10px;
        position: absolute;
        // overflow: hidden;
        right: 0;
        bottom: 0;
    }

    &__author-pfp {
        width: 35px;
        height: 100%;
        border-radius: 50%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: $primePurple;
        overflow: hidden;
    }

    &__pfp-img {
        object-fit: cover;
        height: 100%;
    }
}

