.pricing {
    margin-bottom: 100px;
    width: 100%;
    &__content {
        // border: solid 1px $primeGreen;
        // background-color: lighten($primeGreen, 50%);
        border-radius: 20px;
        text-align: center;
        @media screen and (min-width: 900px) {
            text-align: left;
        }
        p {
            font-size: 1.3rem;
            color: #222222;
            font-weight: 700;
        }
    }
    &__ul {
        padding-left: 30px;

        @media screen and (min-width: 900px) {
            display: flex;
            justify-content: space-between;
        }
        li {
            font-size: 1.3rem;
            color: #222222;
            // width: 100%;
            &::before {
                content: "\2022"; /* Add content: \2022 is the CSS Code/unicode for a bullet */
                color: $primeOrange; /* Change the color */
                font-weight: bold; /* If you want it to be bold */
                display: inline-block; /* Needed to add space between the bullet and the text */
                width: 1.5rem; /* Also needed for space (tweak if needed) */
                margin-left: -1em; /* Also needed for space (tweak if needed) */
                font-size: 2rem;
            }
        }
    }

    &__cta {
        background-color: $primeOrange;
        padding: 20px;
        color: #fff;
        display: inline-block;
        border-radius: 8px;
        font-size: 1.5rem;
        margin-top: 20px;
        transition: 0.3s ease;
        text-decoration: none;

        &:hover {
            background-color: darken($primeOrange, 10%);
        }
    }
}
